<template>
    <b-navbar toggleable="lg" type="light">
        <b-collapse id="nav-collapse" is-nav>
            <!-- Right aligned nav items -->
            <b-navbar-nav class="ml-auto">
                <!-- <b-icon icon="bell-fill"></b-icon> -->

                <b-nav-item-dropdown right>
                    <template #button-content>
                        <b-icon icon="globe"></b-icon>
                        {{ display_lang }}
                    </template>
                    <b-dropdown-item @click="handleChange('ar')" v-if="lang == 'en'"></b-dropdown-item>
                    <b-dropdown-item @click="handleChange('en')" v-if="lang == 'ar'">English</b-dropdown-item>
                </b-nav-item-dropdown>

                <b-nav-item-dropdown right>
                    <!-- Using 'button-content' slot -->
                    <template #button-content>
                        <b-icon icon="person-fill"></b-icon>
                    </template>
                    <b-dropdown-item href="#">Profile</b-dropdown-item>
                    <b-dropdown-item href="#">Sign Out</b-dropdown-item>
                </b-nav-item-dropdown>
            </b-navbar-nav>
        </b-collapse>
    </b-navbar>
</template>

<script>

export default {
    name: 'Navbar',

    data: () => ({
        lang: "",
        display_lang: "",
    }),
    // computed: {
    //     isAr() {
    //         this.lang = localStorage.getItem("lang")
    //         console.log(this.lang)
    //         return this.lang
    //     },
    // },
    methods: {
        handleChange(selectedLang) {
            localStorage.setItem('lang', selectedLang)
            window.location.reload()
            console.log(selectedLang)
        },
    },
    mounted() {
        this.lang = localStorage.getItem("lang")
        if (this.lang == "ar")
            this.display_lang = ""
        else
            this.display_lang = "English"
        // console.log(this.lang)
    }
};
</script>

<style lang="scss">
.navbar {
    background-color: white;
    border-bottom: 1px solid #e6e6e6;
    box-shadow: 0 0 10px #e4e4e4;
}

.navbar .bi-person-fill {
    font-size: 28px !important;
}


.navbar .bi-globe {
    font-size: 25px !important;
}

.navbar-nav .nav-link {
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 10px;
    font-size: 18px;
}

html:lang(ar) .navbar-nav .nav-link {
    direction: rtl;
}
.navbar .dropdown-menu{
    padding: 10px 0px
}
</style>