import Vue from 'vue'
import VueRouter from 'vue-router'

import CategoryList from "@/views/Category/List.vue"
import AddCategory from "@/views/Category/Add.vue"
import EditCategory from "@/views/Category/Add.vue"


import SubCategoryList from "@/views/SubCategory/List.vue"
import AddSubCategory from "@/views/SubCategory/Add.vue"
import EditSubCategory from "@/views/SubCategory/Add.vue"


import PartialCategoryList from "@/views/PartialCategory/List.vue"
import AddPartialCategory from "@/views/PartialCategory/Add.vue"
import EditPartialCategory from "@/views/PartialCategory/Add.vue"



import StateList from "@/views/State/List.vue"
import AddState from "@/views/State/Add.vue"
import EditState from "@/views/State/Add.vue"

import CityList from "@/views/City/List.vue"
import AddCity from "@/views/City/Add.vue"
import EditCity from "@/views/City/Add.vue"


Vue.use(VueRouter)

const routes = [
  {
    path: '/category/list',
    name: 'category-list',
    component: CategoryList
  },
  {
    path: '/category/add',
    name: 'add-category',
    component: AddCategory
  },
  {
    path: '/category/edit/:id',
    name: 'edit-category',
    component: EditCategory
  },


  {
    path: '/sub-category/list',
    name: 'sub-category-list',
    component: SubCategoryList
  },
  {
    path: '/sub-category/add',
    name: 'add-sub-category',
    component: AddSubCategory
  },
  {
    path: '/sub-category/edit/:id',
    name: 'edit-sub-category',
    component: EditSubCategory
  },


  {
    path: '/partial-category/list',
    name: 'partial-category-list',
    component: PartialCategoryList
  },
  {
    path: '/partial-category/add',
    name: 'add-partial-category',
    component: AddPartialCategory
  },
  {
    path: '/partial-category/edit/:id',
    name: 'edit-partial-category',
    component: EditPartialCategory
  },



  {
    path: '/state/list',
    name: 'state-list',
    component: StateList
  },
  {
    path: '/state/add',
    name: 'add-state',
    component: AddState
  },
  {
    path: '/state/edit/:id',
    name: 'edit-state',
    component: EditState
  },

  {
    path: '/city/list',
    name: 'city-list',
    component: CityList
  },
  {
    path: '/city/add',
    name: 'add-city',
    component: AddCity
  },
  {
    path: '/city/edit/:id',
    name: 'edit-city',
    component: EditCity
  },
  
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
