<template>
    <div id="add-partial-category" class="add-partial-category">
        <breadcrumbs :breadcrumbs="bread_add_en" v-if="lang == 'en' && this.route == 'add-partial-category'" />
        <breadcrumbs :breadcrumbs="bread_add_ar" v-if="lang == 'ar' && this.route == 'add-partial-category'" />
        <breadcrumbs :breadcrumbs="bread_edit_en" v-if="lang == 'en' && this.route == 'edit-partial-category'" />
        <breadcrumbs :breadcrumbs="bread_edit_ar" v-if="lang == 'ar' && this.route == 'edit-partial-category'" />
        <div class="card">
            <div class="card-header"  v-if="this.route == 'add-partial-category'">
                {{ $t('partial-category.add') }}
            </div>
            <div class="card-header" v-else-if="this.route == 'edit-partial-category'">
                {{ $t('partial-category.edit') }}
            </div>
            <div class="card-body">
                <v-form>
                    <v-select outlined v-model="sub_id" :reverse="direction" :items="sub_list" item-text="name"
                        item-value="id" :label="$t('sub-category.name')" 
                        :error-messages="subIdErrors"  @input="$v.sub_id.$touch()"></v-select>

                    <div class="row" style="align-items: center;" >
                        <div class="col-lg-10" v-for="(v, index) in $v.partial_category.$each.$iter" :key="index">
                            <div class="row">
                                <div class="col-lg-6">
                                    <v-text-field outlined :reverse="direction" required v-model="v.name_en.$model"
                                        :error-messages="nameEnErrors(index)" @input="v.name_en.$touch()"
                                        :label="$t('partial-category.name_en')"></v-text-field>
                                </div>
                                <div class="col-lg-6">
                                    <v-text-field outlined :reverse="direction" required v-model="v.name_ar.$model"
                                        :error-messages="nameArErrors(index)" @input="v.name_ar.$touch()"
                                        :label="$t('partial-category.name_ar')"></v-text-field>
                                </div>
                            </div>
                        </div>

                        <div class="col-lg-1" style="margin-bottom: 30px">
                            <v-btn color="blue" text class="plus-button" @click="add()">
                                +
                            </v-btn>
                        </div>
                        <div class="col-lg-1" style="margin-bottom: 30px">
                            <v-btn color="blue" text class="plus-button" @click="partialtract()">
                                -
                            </v-btn>
                        </div>
                    </div>



                    <!-- <div class="row" style="align-items: center;" v-if="this.route == 'edit-partial-category'">
                        <div class="col-lg-12">
                            <div class="row">
                                <div class="col-lg-6">
                                    <v-text-field outlined :reverse="direction" required v-model="partial_category.name_en"
                                        :error-messages="nameEnErrors(index)" @input="v.name_en.$touch()"
                                        :label="$t('partial-category.name_en')"></v-text-field>
                                </div>
                                <div class="col-lg-6">
                                    <v-text-field outlined :reverse="direction" required v-model="partial_category.name_ar"
                                        :error-messages="nameArErrors(index)" @input="v.name_ar.$touch()"
                                        :label="$t('partial-category.name_ar')"></v-text-field>
                                </div>
                            </div>
                        </div>
                    </div> -->

                    <v-btn @click="submit" :disabled="isSubmit && !response" color="primary" light style="margin-top: 15px">
                        <v-progress-circular :size="20" v-if="isSubmit && !response" indeterminate
                            color="white"></v-progress-circular>
                        {{ $t('submit') }}
                    </v-btn>
                    <v-snackbar right bottom color="primary" text v-model="snackbar" :timeout="timeout">
                        {{ $t('success_message') }}
                        <template v-slot:action="{ attrs }">
                            <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">
                                {{ $t('close') }}
                            </v-btn>
                        </template>
                    </v-snackbar>
                </v-form>
            </div>
        </div>
    </div>
</template>

<script>
import { validationMixin } from 'vuelidate'
import { required, minLength } from 'vuelidate/lib/validators'
import Breadcrumbs from "@/components/Breadcrumbs.vue"
export default {
    name: 'AddpartialCategory',
    components: {
        Breadcrumbs
    },
    mixins: [validationMixin],

    data: () => ({
        partial_category: [{
            name_en: '',
            name_ar: '',
        }],
        response: false,
        isSubmit: false,
        snackbar: false,
        text: 'Your data was saved successfuly',
        timeout: 3000,
        bread_add_en: [
            {
                text: 'Real Estate',
                disabled: true,
                href: '',
            },
            {
                text: 'Partial Category',
                disabled: true,
                href: '',
            },
            {
                text: 'Add',
                disabled: false,
                href: '/partial-category/add',
            },
        ],
        bread_add_ar: [
            {
                text: '',
                disabled: true,
                href: '',
            },
            {
                text: ' ',
                disabled: true,
                href: '',
            },
            {
                text: '',
                disabled: false,
                href: '/partial-category/add',
            },
        ],
        bread_edit_en: [
            {
                text: 'Real Estate',
                disabled: true,
                href: '',
            },
            {
                text: 'Partial Category',
                disabled: true,
                href: '',
            },
            {
                text: 'Edit',
                disabled: true,
                href: '',
            },
        ],
        bread_edit_ar: [
            {
                text: '',
                disabled: true,
                href: '',
            },
            {
                text: ' ',
                disabled: true,
                href: '',
            },
            {
                text: '',
                disabled: true,
                href: '',
            },
        ],
        direction: false,
        lang: '',
        sub_list: [],
        sub_id: null,
        edit: false,
        route: '',
        id: ''
    }),
    validations: {
        partial_category: {
            $each: {
                name_en: { required, minLength: minLength(3) },
                name_ar: { required, minLength: minLength(3) },
            }
        },
        sub_id: { required }
    },
    computed: {
        subIdErrors() {
            const errors = []
            if (!this.$v.sub_id.$dirty) return errors
            !this.$v.sub_id.required && errors.push('Sub Category is required')
            return errors
        },
    },

    methods: {
        nameArErrors(index) {
            const errors = []
            if (!this.$v.partial_category.$each[index].name_ar.$dirty) return errors
            !this.$v.partial_category.$each[index].name_ar.minLength && errors.push('Name must be at least 3 characters')
            !this.$v.partial_category.$each[index].name_ar.required && errors.push('Name is required')
            return errors
        },
        nameEnErrors(index) {
            const errors = []
            if (!this.$v.partial_category.$each[index].name_en.$dirty) return errors
            !this.$v.partial_category.$each[index].name_en.minLength && errors.push('Name must be at least 3 characters')
            !this.$v.partial_category.$each[index].name_en.required && errors.push('Name is required')
            return errors
        },
        initDir() {
            this.lang = localStorage.getItem('lang')
            if (this.lang == 'en')
                this.direction = false
            else
                this.direction = true
        },
        submit() {
            // console.log(this.$v.$error)
            this.$v.$touch()
            if (!this.$v.$error) {
                this.isSubmit = true
                console.log(this.partial_category)
                this.sendData()
            }
        },
        sendData() {
            const formData = new FormData()
            formData.append('sub_id', this.sub_id)
            if (this.edit == true) {
                this.editData(formData)
            }
            else {
                this.addData(formData)
            }

        },
        addData(formData) {
            formData.append('list', JSON.parse(this.partial_category))
            for (var pair of formData.entries()) {
                console.log(pair)
            }
            this.axios.post(this.$store.state.url + "/api/add_child", formData)
                .then(res => {
                    console.log(res.data)
                    this.response = true
                    if (res.status == 200)
                        this.snackbar = true
                });
        },
        editData(formData) {
            formData.append('id', this.id)
            formData.append('name_en', this.partial_category[0].name_en)
            formData.append('name_ar', this.partial_category[0].name_ar)
            for (var pair of formData.entries()) {
                console.log(pair[0]+ ': ' + pair[1])
            }
            this.axios.post(this.$store.state.url + "/api/update_child", formData)
                .then(res => {
                    console.log(res)
                    this.response = true
                    if (res.data.error == 'false') {
                        this.snackbar = true
                        // this.name_ar = null
                        // this.name_en = null
                        // this.$v.$reset()
                    }
                });
        },
        getSubCategory() {
            this.axios.get(this.$store.state.url + "/api/get_sub?lang=" + this.lang)
                .then(res => {
                    if (res.data.error == 'false')
                        this.sub_list = res.data.data
                });
        },
        add() {
            this.partial_category.push({
                name_ar: '',
                name_en: '',
            });
            // console.log(this.partial_category)
        },
        subtract() {
            if (this.partial_category.length > 1)
                this.partial_category.pop()
            // console.log(this.partial_category)
        },
        initEdit() {
            this.route = this.$route.name
            if (this.$route.name == "edit-partial-category") {
                this.id = this.$route.params.id
                this.edit = true
                this.axios.get(this.$store.state.url + "/api/edit_child?id=" + this.id)
                    .then(res => {
                        this.sub_id = res.data.data.sub_id
                        this.$v.partial_category.$each[0].name_ar.$model = res.data.data.name.ar
                        this.$v.partial_category.$each[0].name_en.$model = res.data.data.name.en
                    })
            }
        },
    },
    mounted() {
        this.initDir()
        this.getSubCategory()
        this.initEdit()
    }
};
</script>

<style lang="scss">
@import url(../../assets/Css/main.css);

html:lang(ar) .plus-button {
    font-size: 30px !important;
    padding-bottom: 6px !important;
}

html:lang(en) .plus-button {
    font-size: 30px !important;
    padding-bottom: 0px !important;
}

.my-box {
    border: 1px solid #adadad;
    border-radius: 20px;
    padding: 30px 40px;
    margin-bottom: 40px;
}
</style>
