<template>
  <v-app :class="[isAr ? 'ar' : 'en']">
    <div :class="[isCollapsed ? 'collapsed' : 'not-collapsed']">

      <v-main>
        <sidebar />
        <navbar />
        <router-view />
      </v-main>
    </div>

  </v-app>
</template>

<script>
import Sidebar from '@/components/Sidebar'
import Navbar from '@/components/Navbar.vue';
export default {
  name: 'App',
  components: {
    Sidebar,
    Navbar
  },
  data: () => ({
    lang: '',
  }),
  computed: {
    isAr() {
      this.lang = localStorage.getItem("lang")
      let htmlElement = document.documentElement
      if (this.lang == 'ar') {
        htmlElement.setAttribute('lang', 'ar')
        return true
      }
      else {
        htmlElement.setAttribute('lang', 'en')
        return false
      }
    },
    isCollapsed() {
      return this.$store.state.isCollapsed
    }
  },
  created() {
    this.lang = localStorage.getItem("lang")
    if (this.lang == null)
      localStorage.setItem("lang", "en")
  },


};
</script>

<style lang="scss">
html:lang(ar) * {
  letter-spacing: 0 !important;
}

.v-main__wrap {
  background-color: #f2f2f2;
}

@media (max-width: 767px) {
  html:lang(en) .v-main {
    padding-left: 65px !important;
  }

  html:lang(at) .v-main {
    padding-right: 65px !important;
  }
}

@media (min-width: 767px) {
  html:lang(en) .collapsed {
    padding-left: 65px;
    transition: 0.3s padding-left ease;
  }

  html:lang(en) .not-collapsed {
    padding-left: 300px;
    transition: 0.3s padding-left ease;
  }

  html:lang(ar) .collapsed {
    padding-right: 65px;
    transition: 0.3s padding-right ease;
  }

  html:lang(ar) .not-collapsed {
    padding-right: 300px;
    transition: 0.3s padding-right ease;
  }
}

@font-face {
  font-family: "Poppins-Regular";
  src: local("Poppins-Regular"),
    url(assets/font/Poppins-Regular.ttf) format("truetype");
  font-display: swap
}

@font-face {
  font-family: "Almarai";
  src: local("Almarai"),
    url(assets/font/Almarai-Regular.ttf) format("truetype");
}

.en {
  font-family: "Poppins-Regular", sans-serif !important;
  direction: ltr;
}

.ar {
  font-family: 'Almarai', sans-serif !important;
  direction: rtl;
}
</style>
