<template>
    <div id="add-state" class="add-state">
        <breadcrumbs :breadcrumbs="bread_add_en" v-if="lang == 'en' && this.route == 'add-state'" />
        <breadcrumbs :breadcrumbs="bread_add_ar" v-if="lang == 'ar' && this.route == 'add-state'" />
        <breadcrumbs :breadcrumbs="bread_edit_en" v-if="lang == 'en' && this.route == 'edit-state'" />
        <breadcrumbs :breadcrumbs="bread_edit_ar" v-if="lang == 'ar' && this.route == 'edit-state'" />
        <div class="card">
            <div class="card-header" v-if="this.route == 'add-state'">
                {{ $t('state.add') }}
            </div>
            <div class="card-header" v-else-if="this.route == 'edit-state'">
                {{ $t('state.edit') }}
            </div>
            <div class="card-body">
                <v-form>
                    <v-text-field outlined :reverse="direction" required v-model="name_en" :error-messages="nameEnErrors"
                        @input="$v.name_en.$touch()" :label="$t('state.name_en')"></v-text-field>
                    <v-text-field outlined :reverse="direction" required v-model="name_ar" :error-messages="nameArErrors"
                        @input="$v.name_ar.$touch()" :label="$t('state.name_ar')"></v-text-field>
                    <v-btn @click="submit" :disabled="isSubmit && !response" color="primary" light style="margin-top: 15px">
                        <v-progress-circular :size="20" v-if="isSubmit && !response" indeterminate
                            color="white"></v-progress-circular>
                        {{ $t('submit') }}
                    </v-btn>
                    <v-snackbar right bottom color="primary" text v-model="snackbar" :timeout="timeout">
                        {{ $t('success_message') }}
                        <template v-slot:action="{ attrs }">
                            <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">
                                {{ $t('close') }}
                            </v-btn>
                        </template>
                    </v-snackbar>
                </v-form>
            </div>
        </div>
    </div>
</template>

<script>
import { validationMixin } from 'vuelidate'
import { required, minLength } from 'vuelidate/lib/validators'
import Breadcrumbs from "@/components/Breadcrumbs.vue"
export default {
    name: 'AddState',
    components: {
        Breadcrumbs
    },
    mixins: [validationMixin],

    data: () => ({
        name_en: '',
        name_ar: '',
        response: false,
        isSubmit: false,
        snackbar: false,
        timeout: 3000,
        bread_add_en: [
            {
                text: 'Real Estate',
                disabled: true,
                href: '',
            },
            {
                text: 'States',
                disabled: true,
                href: '',
            },
            {
                text: 'Add',
                disabled: false,
                href: '/state/add',
            },
        ],
        bread_add_ar: [
            {
                text: '',
                disabled: true,
                href: '',
            },
            {
                text: '',
                disabled: true,
                href: '',
            },
            {
                text: '',
                disabled: false,
                href: '/state/add',
            },
        ],
        bread_edit_en: [
            {
                text: 'Real Estate',
                disabled: true,
                href: '',
            },
            {
                text: 'States',
                disabled: true,
                href: '',
            },
            {
                text: 'Edit',
                disabled: true,
                href: '',
            },
        ],
        bread_edit_ar: [
            {
                text: '',
                disabled: true,
                href: '',
            },
            {
                text: '',
                disabled: true,
                href: '',
            },
            {
                text: '',
                disabled: true,
                href: '',
            },
        ],
        direction: false,
        lang: '',
        edit: false,
        route: '',
        id: ''
    }),

    validations: {
        name_en: { required, minLength: minLength(3) },
        name_ar: { required, minLength: minLength(3) },
    },
    computed: {
        nameEnErrors() {
            const errors = []
            if (!this.$v.name_en.$dirty) return errors
            !this.$v.name_en.minLength && errors.push('Name must be at least 3 characters')
            !this.$v.name_en.required && errors.push('Name is required')
            return errors
        },
        nameArErrors() {
            const errors = []
            if (!this.$v.name_ar.$dirty) return errors
            !this.$v.name_ar.minLength && errors.push('Name must be at least 3 characters')
            !this.$v.name_ar.required && errors.push('Name is required')
            return errors
        },
    },

    methods: {
        initDir() {
            this.lang = localStorage.getItem('lang')
            if (this.lang == 'en')
                this.direction = false
            else
                this.direction = true
        },
        submit() {
            this.$v.$touch()
            console.log(this.$v.$error)
            if (!this.$v.$error) {
                this.isSubmit = true
                this.sendData()
            }
        },

        sendData() {
            const formData = new FormData()
            formData.append('name.en', this.name_en)
            formData.append('name.ar', this.name_ar)
            if (this.edit == true) {
                this.editData(formData)
            }
            else {
                this.addData(formData)
            }
        },
        addData(formData) {
            this.axios.post(this.$store.state.url + "/api/add_state", formData)
                .then(res => {
                    console.log(res)
                    this.response = true
                    if (res.data.error == 'false') {
                        this.snackbar = true
                        this.name_ar = null
                        this.name_en = null
                        this.$v.$reset()
                    }
                });
        },
        editData(formData){
            formData.append('id', this.id)
            this.axios.post(this.$store.state.url + "/api/update_state", formData)
                .then(res => {
                    console.log(res)
                    this.response = true
                    if (res.data.error == 'false') {
                        this.snackbar = true
                        // this.name_ar = null
                        // this.name_en = null
                        // this.$v.$reset()
                    }
                });
        },
        initEdit() {
            this.route = this.$route.name
            if (this.$route.name == "edit-state") {
                this.id = this.$route.params.id
                this.edit = true
                this.axios.get(this.$store.state.url + "/api/edit_state?id=" + this.id)
                    .then(res => {
                        console.log(res.data.data)
                        this.name_ar = res.data.data.name.ar
                        this.name_en = res.data.data.name.en
                    })
            }
        },

    },
    mounted() {
        this.initDir()
        this.initEdit()
    }
};
</script>

<style lang="scss">
@import url(../../assets/Css/main.css);
</style>
