<template>
    <div id="list-category" class="list-category list">
        <breadcrumbs :breadcrumbs="bread_en" v-if="lang == 'en'" />
        <breadcrumbs :breadcrumbs="bread_ar" v-if="lang == 'ar'" />
        <div class="card">
            <div class="card-header">
                {{$t('category.list')}}
            </div>
            <div class="card-body">
                <div v-if="load">
                    <v-card-title>
                        <v-spacer></v-spacer><v-spacer></v-spacer><v-spacer></v-spacer>
                        <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line
                            hide-details></v-text-field>
                    </v-card-title>
                    <v-data-table :headers="$t('category.headers')" :items="data" :search="search" class="my-table">
                        <template v-slot:top>
                            <v-dialog v-model="dialogDelete" max-width="500px">
                                <v-card>
                                    <v-spacer></v-spacer>
                                    <v-card-title class="justify-content-center" style="padding-top: 30px">
                                        {{ $t('confrim_delete') }}
                                    </v-card-title>
                                    <v-card-actions style="padding-bottom: 30px">
                                        <v-spacer></v-spacer>
                                        <v-btn color="gray" text @click="closeDelete">{{ $t('cancel') }}
                                        </v-btn>
                                        <v-btn color="red darken-1" text @click="deleteItemConfirm">{{$t('confrim')}}
                                        </v-btn>
                                        <v-spacer></v-spacer>
                                    </v-card-actions>
                                </v-card>
                            </v-dialog>
                        </template>
                        <template v-slot:[`item.mangement`]="{ item }">
                            <div class="row justify-content-center">
                                <div @click="deleteItem(item)">
                                    <i class="fa fa-trash"></i>
                                </div>
                                <div @click="editItem(item)">
                                    <i class="fas fa-pen"></i>
                                </div>
                            </div>
                        </template>
                    </v-data-table>
                </div>
                <div class="progress-container" v-else>
                    <v-progress-circular indeterminate color="primary" :size="50"
                        style="margin-top: 100px"></v-progress-circular>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Breadcrumbs from "@/components/Breadcrumbs.vue"

export default {
    name: 'CategoryList',
    components: {
        Breadcrumbs
    },
    data: () => ({
        search: '',
        lang: '',
        data: [],
        load: false,
        dialogDelete: false,
        editedIndex: -1,
        delete: '',
        bread_en: [
            {
                text: 'Real Estate',
                disabled: true,
                href: '',
            },
            {
                text: 'Categories',
                disabled: true,
                href: '',
            },
            {
                text: 'List',
                disabled: false,
                href: '/category/list',
            },
        ],
        bread_ar: [
            {
                text: '',
                disabled: true,
                href: '',
            },
            {
                text: '',
                disabled: true,
                href: '',
            },
            {
                text: '',
                disabled: false,
                href: '/category/list',
            },
        ]
    }),
    watch: {
        dialogDelete(val) {
            val || this.closeDelete()
        },
    },
    methods: {
        getData() {
            this.axios.get(this.$store.state.url + "/api/category?lang="+this.lang)
                .then(res => {
                    console.log(res.data.data)
                    this.load = true
                    if (res.data.error == 'false')
                        this.data = res.data.data
                });
        },
        editItem(item) {
            console.log("Edit: " + item.id)
            this.$router.replace({ name: 'edit-category', params: { id: item.id } })
        },
        deleteItem(item) {
            console.log("Delete: " + item.id)
            this.editedIndex = this.data.indexOf(item)
            this.delete = item
            this.dialogDelete = true
        },
        deleteItemConfirm() {
            this.data.splice(this.editedIndex, 1)
            this.sendIdDeleted()
            this.closeDelete()
        },
        closeDelete() {
            this.dialogDelete = false
        },
        sendIdDeleted() {
            this.axios.post(this.$store.state.url + "/api/category_delete", { id: this.delete.id })
                .then((res) => {
                    console.log(res.data)
                    console.log(this.delete.id)
                })
        },
    },
    mounted() {
        this.lang = localStorage.getItem('lang')
        this.getData()
    }
};
</script>

<style lang="scss">
@import url(../../assets/Css/main.css);
</style>
