  name: 'Sidebar',
  data: () => ({
    direction: false,
  }),
  methods: {
    onToggleCollapse(collapsed) {
      this.$store.state.isCollapsed = collapsed
    },
  },
  mounted() {
    const lang = localStorage.getItem('lang')
    if (lang == 'en')
      this.direction = false
    else
      this.direction = true
  }
}
